*{
    box-sizing: border-box;
}
.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
#root{
    min-height: 100vh;
}

/*#region Reset */

body {
    margin: 0;
    background-color: #252525;
}
a {
    text-decoration: none;
}
h1,h2,h3,h4 {
    margin: 0;
    padding: 0;
}
ul {
    list-style: none;
    padding: 0;
    margin-block-start: 0;
    margin-block-end: 0;
}
p {
    margin: 0;
    padding: 0;
}

/*#endregion */

/*#region Common */

.section__header{
    font-size: clamp(3rem, 1rem + 3.6941vw, 11rem);
    color: #FFFCF2;
    padding-bottom: 2rem;
}
.highlighted{
    color: #1009c9;
    font-weight: bold;
}
.highlighted::selection{
    color: yellow;
}
::selection {
    color: darkviolet;
    background: black;
}

/*#endregion */

/*#region Fonts*/

.navbar {
    font-family: 'Bebas Neue', sans-serif;
}

.header__top {
    font-family: 'Bebas Neue', sans-serif;
}

.footer {
    font-family: 'Bebas Neue', sans-serif;
}

.about {
    font-family: 'Bebas Neue', sans-serif;
}

.project {
    font-family: 'Bebas Neue', sans-serif;
}

/*#endregion*/




.project-main-btns_with_info {
    display: flex;
}







.descr-text{
    word-break: break-word;
}


.project-main-descr-card {
    margin: 0 0 40px 0 ;
    text-align: left;
    font-family: 'Space Grotesk', monospace;
}





.test {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    z-index: -1;
    top: -3px;
}








.descr-text a{
  color: #44ffaa;
}

.descr-text ol{
    padding-left: 1.2em;
    display: flex;
    row-gap: 14px;
    flex-direction: column;
}


